
'use client';
import Link from 'next/link';
import { useAuth } from '@/contexts/AuthContext';

const Header = () => {
  const { user, loading, logout, userRole } = useAuth();

  return (
    <header className="bg-gray-800 text-white p-4 flex justify-between items-center">
      <div className="flex items-center">
        <Link href="/">
          <h1 className="text-xl font-bold">Veterinaria</h1>
        </Link>
      </div>
      <nav>
        <ul className="flex space-x-4">
          {loading ? (
            <p>Cargando...</p>
          ) : user ? (
            <>
              <li>
                <span className="mr-4">Hola, {user.displayName || user.email}</span>
              </li>
              {userRole === 'admin' && (
                <li>
                  <Link href="/admin">
                    <span className="hover:text-gray-300">Administración</span>
                  </Link>
                </li>
              )}
              <li>
                <button onClick={logout} className="hover:text-gray-300">
                  Cerrar Sesión
                </button>
              </li>
            </>
          ) : (
            <li>
              <Link href="/login">
                <span className="hover:text-gray-300">Iniciar Sesión</span>
              </Link>
            </li>
          )}
        </ul>
      </nav>
    </header>
  );
};

export default Header;


